body {
  background-color: #f9f9f9;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.loader {
  color: #ff4500;
  font-weight: bold;
}

.error {
  color: red;
  font-weight: bold;
}

.cat-info img {
  cursor: pointer;
  border: 5px solid #ff4500;
  border-radius: 50%;
  width: 300px;
  height: 300px;
  margin: 10px;
  padding: 5px;
  transition: transform .5s;
}

.cat-info img:hover {
  transform: scale(1.1);
}

.cat-info h2 {
  color: #ff4500;
  font-family: Georgia, Times New Roman, Times, serif;
}

.slim-select .ss-main {
  border: 1px solid #ff4500;
  padding: 10px;
  font-size: 16px;
}
